import React from "react";
import connect from "./store";

export const App = ({ store, actions }) => {
  console.log("Store.user is as follows: ", store.user);
  console.log("Store.user.results is as follows: ", store.user.results);
  const allData = store.user.results[0];
  console.log("Store.user.results[0] is as follows: ", allData);
  console.log("Store.user.results[0].gender is as follows: ", allData.gender);
  return (
    <div>
      <button onClick={actions.increment}>Clicked {store.count} times</button>
      <button onClick={actions.fetchUser}>fetch user</button>

      <div style={{ backgroundColor: "#ff0000" }}>
        {(store.user.gender, store.user, 2)}
        {allData.gender}
      </div>

      <pre>
        <code>{JSON.stringify(store.user, null, 2)}</code>
      </pre>
    </div>
  );
};

export default connect(App);
